import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { CountriesService } from 'app/shared/services/countries.service';
import { MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { GovtSubsidyService } from 'app/shared/services/ev-space/govt-subsidy/govt-subsidy.service';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ViewDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  filteredOptionState: Observable<any[]>;
  filteredOptionsVehicle:Observable<any[]>;
  stateName:any;
 
  vehicles:any;
  states:any;
  state_id:any;
  @ViewChild("toggleElement") ref: any;
  status = false;
  errorMsg:String;
  splitted:String;
  localStorage:any;
  filteredOptionsClient: Observable<any[]>;
  isPublish:boolean = false;
  isModrate:boolean = false;

  constructor(private fb: FormBuilder,private govtSubsidyService:GovtSubsidyService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<ViewDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, public countryService: CountriesService) { }
  ngOnInit() {
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.params = this.data;

    this.isEditMode = this.params.action == 'CREATE' ? false : true;
    this.isPublish = this.params.buttonAction == 'publishAction'? true:false;
    this.isModrate = this.params.buttonAction == 'modrateAction'? true:false;
    this.getVehicle();
    this.getStatesByCountry(1);
    this.form = this.fb.group({
      state:['',[autocompleteObjectValidator(),Validators.required]],
      battery_kwh: ['', [Validators.required,Validators.pattern(/^[0-9]*$/)]],
      capacity : ['', [Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]] ,
      max_subsidy: [''],
      road_tax_exemption : ['' ] ,     
      vehicle:['',[autocompleteObjectValidator(),Validators.required]],
     status : this.status == true ? 'M' : 'N'
    });
    
    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    this.getAllState(1);
  }
    getAllState(countryID){
      this.countryService.getStateByCountry(countryID).subscribe(
        (data:any)=>{
          //;
          this.states = data;
          if(this.isEditMode){
            //;
            const toSelect = this.states.find(c => c.id == this.params.data.state_id);
            this.form.get('state').setValue(toSelect);
           
            this.filteredOptionState = this.form.controls.state.valueChanges
             .pipe(
            startWith(''),
            //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );
          }
          
           },(err)=>{
             
           }
      )
    }
    
   
    getVehicle() {
      this.govtSubsidyService.getVehicleTypes().subscribe(
        (resp: any) => {
          this.vehicles = resp;
  
          if (this.isEditMode) {
  
            const toSelect = this.vehicles.find(c => c.id == this.params.data.vehicle_type_id);
            this.form.get('vehicle').setValue(toSelect);
          }
          this.filteredOptionsVehicle = this.form.controls.vehicle.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterVehicleModels(name) : this.vehicles)
            );
        }, (err) => {
  
        }
      )
  
    }
  
    close() {
  
      this.dialogRef.close();
    }
    closeActionPanel(){
      this.dialogRef.close();
    }
  
    onYesClick() {
      if(this.form.valid){
        let formValues = this.form.value;
        const params: any = {
          state_id : formValues.state.id,
          per_KWh_of_battery:formValues.battery_kwh,
          capacity:formValues.capacity,
          max_subsidy:formValues.max_subsidy,
          road_tax_exemption:formValues.road_tax_exemption,
          vehicle_type_id:formValues.vehicle.vehicle_type_id,
          status :  this.status == true ? 'M' : 'N',   
        }
    
        if (this.isEditMode) {
          params.id = this.params.data.id,
            params.modify_by = this.localStorage.userId;
        } else {
          params.created_by = this.localStorage.userId;
        }
    
        if (this.isEditMode) {
          this.govtSubsidyService.updateSubsidy(params).subscribe(
            (resp: any) => {
              if(resp.status == true){
                this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
                this.dialogRef.close();
              }
              
            },
            (err) => {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
            }
          )
        } else {
          this.govtSubsidyService.subsidy(params).subscribe(
            (data: any) => {
              this.snackBar.open(MessageConstant.subsidyCreate, 'Dismiss', { duration: 5000 });
              this.dialogRef.close();
            },
            (err) => {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
            }
          )
    
        }
    
        this.dialogRef.close(params);
      }else{
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
      }
      
    }
  
    getErrorMessage(form: FormGroup) {
      return form.get('email').hasError('required')
        ? 'You must enter a value'
        : form.get('email').hasError('email')
          ? 'Not a valid email'
          : '';
    }
  
    assignValues(params) {
  
      this.form.get('battery_kwh').setValue(params.data.per_KWh_of_battery);
    this.form.get('capacity').setValue(params.data.capacity);
    this.form.get('max_subsidy').setValue(params.data.max_subsidy);
    this.form.get('road_tax_exemption').setValue(params.data.road_tax_exemption);
      
      this.status = params.data.status == 'M' ? true : false;
    }
  
    updateStatus(event) {
      this.status = event.checked;
      this.ref.checked = this.status;
    }
    displayVehicle(item: any): string {
      return item && item.name ? item.name : '';
    }
    private _filterVehicleModels(name: string): any[] {
      const filterValue = name.toLowerCase();
      return this.vehicles.filter(option => option.name.toLowerCase().includes(filterValue));
    }
   
    getStatesByCountry(state) {
      this.state_id = state.id;
      this.countryService.getStateByCountry(this.state_id).subscribe(
        (statedata: any) => {
          //this.form.reset();
          this.states = statedata;
          // const toSelect = this.states.find(c => c.id == data[0].state_id);
          // this.form.get('state').setValue(toSelect);
  
          this.filteredOptionState = this.form.controls.state.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterState(name) : this.states)
            );
  
        }, (err) => {
  
        }
      )
    }
   
   
    displayState(item: any): string {
      return item && item.name ? item.name : '';
    }
    private _filterState(name: string): any[] {
      const filterValue = name.toLowerCase();
      return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
    }
    onPublish(){
      const params: any = {
        id : this.params.data.id,
        status:"Y",
      modifyby :this.localStorage.userId
      }
      this.govtSubsidyService.publishSubsidy(params).subscribe(
        (resp: any) => {
          if(resp.status == true){
            this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          }
          
        },
        (err) => {
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          this.dialogRef.close();
        }
      )
    }
    onModrate(){
      const params: any = {
        id : this.params.data.id,
        status:"M",
      modifyby :this.localStorage.userId
      }
      this.govtSubsidyService.moderateSubsidy(params).subscribe(
        (resp: any) => {
          if(resp.status == true){
            this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          }
          
        },
        (err) => {
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          this.dialogRef.close();
        }
      )
    }
}





