import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TABS_DEMO_ROUTES } from './subsidy-crud/routes';
import { SubsidyCrudComponent } from './subsidy-crud/subsidy-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : SubsidyCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
     
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class SubsidyRoutingModule{}